@import "~style";

$block-name: "b-button";

.#{$block-name} {
	font-size: 13px;
	font-family: "Open Sans";
	font-style: normal;
	font-weight: 600;
	line-height: normal;
	text-transform: uppercase;
	color: #841844;
	border: 2px solid var(--brand-color, #841844);
	background-color: #fff;
	text-align: center;

	&:hover {
		color: #fff;
		border: 2px solid #841844;
		background: #841844;
	}
}
